






















.about::v-deep {
	.markdown-body {
		p {
			margin-top: 0;
			margin-bottom: 16px;
		}
		h1,
		h2,
		h3 {
			margin-top: 24px;
			margin-bottom: 16px;
			font-weight: 600;
			line-height: 1.25;
			&::before {
				content: '# ';
				color: #4a8576;
			}
		}
		h1 {
			font-size: 3em;
			padding: 0;
			margin-top: 0;
		}
		h2 {
			font-size: 2em;
		}
		h3 {
			font-size: 1.5em;
		}
	}
}
